                        </label>
                        <div class="p-col">
                            <div class="p-formgroup-inline">
                                <div class="p-field-checkbox">
                                    <p-radio-button
                                        v-model="form.preImplGeneticTesting"
                                        name="hospitalization"
                                        value="yes"
                                        :class="{
                                            'p-invalid':
                                                v$.preImplGeneticTesting
                                                    .$invalid
                                        }"
                                    />
                                    <label>Yes</label>
                                </div>
                                <div class="p-field-checkbox">
                                    <p-radio-button
                                        v-model="form.preImplGeneticTesting"
                                        name="hospitalization"
                                        value="no"
                                        :class="{
                                            'p-invalid':
                                                v$.preImplGeneticTesting
                                                    .$invalid
                                        }"
                                    />
                                    <label>NO</label>
                                </div>
                                <div class="p-field-checkbox">
                                    <i
                                        v-if="v$.preImplGeneticTesting.$invalid"
                                        class="pi pi-info-circle p-mt-2"
                                        v-tooltip.right="
                                            v$.preImplGeneticTesting.validator
                                                .$message
                                        "
                                        style="color: #f44336"
                                    ></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Characteristic of transferred embryo
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.charTransferredEmbryo"
                                :options="charTransferredEmbryoOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid':
                                        v$.charTransferredEmbryo.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.charTransferredEmbryo.$invalid"
                            class="pi pi-info-circle p-ml-2 p-mt-2"
                            v-tooltip.right="
                                v$.charTransferredEmbryo.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Number of thaws for embryo(s) (highest of
                            transferred)
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.numberThaws"
                                :options="numberThawsOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid': v$.numberThaws.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.numberThaws.$invalid"
                            class="pi pi-info-circle p-ml-2 p-mt-2"
                            v-tooltip.right="v$.numberThaws.validator.$message"
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Site of PGT testing of embryo
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.sitePGTembryo"
                                :options="sitePGTembryoOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid': v$.sitePGTembryo.$invalid
                                }"
                            />
                        </div>
                        <i
                            v-if="v$.sitePGTembryo.$invalid"
                            class="pi pi-info-circle p-ml-2 p-mt-2"
                            v-tooltip.right="
                                v$.sitePGTembryo.validator.$message
                            "
                            style="color: #f44336"
                        ></i>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="">
                            Gestational Carrier (surrogate) radio
                        </label>
                        <div class="p-col">
                            <div class="p-formgroup-inline">
                                <div class="p-field-checkbox">
                                    <p-radio-button
                                        v-model="
                                            form.gestationalCarrierSurrogate
                                        "
                                        value="yes"
                                        :class="{
                                            'p-invalid':
                                                v$.gestationalCarrierSurrogate
                                                    .$invalid
                                        }"
                                    />
                                    <label>Yes</label>
                                </div>
                                <div class="p-field-checkbox">
                                    <p-radio-button
                                        v-model="
                                            form.gestationalCarrierSurrogate
                                        "
                                        value="no"
                                        :class="{
                                            'p-invalid':
                                                v$.gestationalCarrierSurrogate
                                                    .$invalid
                                        }"
                                    />
                                    <label>NO</label>
                                </div>
                                <i
                                    v-if="
                                        v$.gestationalCarrierSurrogate.$invalid
                                    "
                                    class="pi pi-info-circle p-ml-2 p-mt-2"
                                    v-tooltip.right="
                                        v$.gestationalCarrierSurrogate.validator
                                            .$message
                                    "
                                    style="color: #f44336"
                                ></i>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Gestational Carrier (surrogate) chart
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.gestationalCarrierSurrogateChart"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Height of Embryo Recipient(cm)
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.heightRecipient"
                                :max="999"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            Weight of Embryo Recipient(kg)
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.weightRecipient"
                                mode="decimal"
                                :max="9999.9"
                                :minFractionDigits="2"
                                :maxFractionDigits="2"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-ml-2">
                    <div class="p-field p-grid">
                        <label for="" class="p-mt-2">
                            BMI of Embryo Recipient(kg/m<sup>2</sup>)
                        </label>
                        <div class="p-col">
                            <p-input-number
                                v-model="form.recipientBMI"
                                mode="decimal"
                                :max="9999.9"
                                :minFractionDigits="2"
                                :maxFractionDigits="2"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :disabled="v$.$invalid"
            />

            <p-button
                icon="pi pi-check"
                label="Mark As Completed"
                @click="markAsComplete('embryo-transfer')"
                class="p-ml-2"
            />
        </template>
    </p-card>
</template>

<script>
import { inject, onMounted, onUnmounted, toRefs, watch } from "vue";
import {
    useBornEmbryoTransferInit,
    useBornMarkAsComplete
} from "@/composable/born";
import { emitter } from "@/event-bus";
export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            v$,
            fillForm,
            reasonsNoETOptions,
            preparationUterineLiningOptions,
            assistedZonaHatchingOptions,
            sourceTransferredEmbryosOptions,
            transferInseminationMethodOptions,
            charTransferredEmbryoOptions,
            numberThawsOptions,
            sitePGTembryoOptions,
            save,
            getDefaultEndometrialThickness
        } = useBornEmbryoTransferInit();

        watch(() => store.state.born.embryoTransfer, fillForm);

        const { isInBornPage } = toRefs(props);

        const bornForm = inject("bornForm", null);
        //
        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.embryoTrnafer = { ...newValue };
        });

        watch(
            () => store.state.born.embryoTransfer,
            (data) => {
                fillForm(data);
            }
        );

        watch(
            () => form.embryoTransfer,
            (newVal) => {
                if (newVal === "no") {
                    // form.transferDate = null;
                    form.embryosTransferred = null;
                    form.sourceTransferredEmbryos = [];
                } else {
                    form.reasonsNoET = null;
                }
                store.commit("born/SET_EMBRYO_TRANSFER", newVal);
            }
        );

        watch(
            () => form.embryosTransferred,
            (newVal) => {
                if (newVal == 1) {
                    form.numberThaws = "One";
                } else if (newVal == 2) {
                    form.numberThaws = "Two";
                } else if (newVal == 3) {
                    form.numberThaws = "Three";
                } else if (newVal > 3) {
                    form.numberThaws = "More than three";
                } else {
                    form.numberThaws = "Unknown";
                }
            }
        );

        watch(
            () => form.reasonsNoET,
            (newVal) => {
                if (newVal) {
                    // form.embryoTransfer = "no";
                }
            }
        );

        watch(
            () => store.state.born.dependentOn.pgtAPlanned,
            (newVal) => {
                if (newVal == "Y") {
                    form.preImplGeneticTesting = "yes";
                }
            }
        );

        watch(
            () => form.preImplGeneticTesting,
            (newVal) => {
                if (newVal == "yes") {
                    if (form.sitePGTembryo == null) {
                        form.sitePGTembryo = "SITE003";
                    }
                } else {
                    if (form.sitePGTembryo == "SITE003") {
                        form.sitePGTembryo = null;
                    }
                }
            }
        );

        watch(
            () => store.state.born.freshEmbryos,
            (newVal) => {
                store.commit(
                    "born/SET_UTILIZABLE_EMBRYOS",
                    newVal.utilizableEmbryos
                );
                store.commit(
                    "born/SET_EMBRYOS_CRYOPRESERVED",
                    newVal.embryosCryopreserved
                );
            }
        );

        watch(
            () => store.state.born.frozenEmbryos,
            (newVal) => {
                store.commit(
                    "born/SET_UTILIZABLE_AFTER_THAW",
                    newVal.utilizableAfterThaw
                );
            }
        );

        watch(
            () => store.state.cycleMonitoring.lines,
            (newVal) => {
                if (form.endometrialThickness == null) {
                    form.endometrialThickness = getDefaultEndometrialThickness();
                }
            }
        );

        onMounted(() => {
            emitter.on("born:save-temp", save);

            if (store.state.artCycle.selectedCycle != undefined) {
                if (store.state.cycleMonitoring.lines.length == 0) {
                    store.dispatch(
                        "cycleMonitoring/getCycleMonitoringDetails",
                        store.state.artCycle.selectedCycle.cycleId
                    );
                }

                if (store.state.artCycle.selectedCycle.cycleType === "FET") {
                    form.embryoTransfer = "yes";
                }

                if (store.state.born.dependentOn.pgtAPlanned == "Y") {
                    form.preImplGeneticTesting = "yes";
                }

                if (isInBornPage.value == true) return;

                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            }
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        const { markAsComplete } = useBornMarkAsComplete();

        return {
            isInBornPage,
            form,
            v$,
            save,
            reasonsNoETOptions,
            preparationUterineLiningOptions,
            assistedZonaHatchingOptions,
            sourceTransferredEmbryosOptions,
            transferInseminationMethodOptions,
            charTransferredEmbryoOptions,
            numberThawsOptions,
            sitePGTembryoOptions,
            markAsComplete
        };
    }
};
</script>

<style scoped>
.p-field.p-grid > label {
    align-items: flex-start;
}
</style>
